@import url(https://fonts.googleapis.com/css2?family=Oranienbaum&family=Prompt:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.brew{display:flex;justify-content:center;align-items:center}.brew button{font-size:2.75rem;text-transform:uppercase}.brew .text{display:inline-flex;justify-content:space-between;align-items:baseline;width:9.5rem;margin-bottom:1rem}.brew .count{display:inline-block;margin-left:.15ch;font-size:5rem;color:#5F5E5E}.brew .count.number-1{margin-right:.15ch}

.controls{display:flex;justify-content:space-between;align-items:center;padding:0 5vw}.controls button{-webkit-appearance:none;-moz-appearance:none;appearance:none;display:flex;justify-content:center;align-items:center;width:5rem;height:5rem;position:relative;top:-1rem;border:none;border-radius:100%;background-color:rgba(255,119,37,0.08);color:rgba(255,119,37,0.52);font-family:'Oranienbaum', serif;font-size:1.6rem}.controls button span{display:inline-block;margin-top:2rem}

.color{display:grid;grid-template-rows:25% 25% 25% 25%;grid-template-columns:auto;color:#c7c4c4}.color .active{width:8rem;margin:0 auto;color:rgba(255,255,255,0.86);color:#5F5E5E;border-radius:1px;border:solid 1px white}.color .active.border .black{border-color:#551610}.color .active.border .oolong{border-color:#BE873E}.color .active.border .green{border-color:#ccbd56}.color .active.border .white{border-color:#E1DB9B}.color .active.bg .black{background-color:#551610}.color .active.bg .oolong{background-color:#BE873E}.color .active.bg .green{background-color:#ccbd56}.color .active.bg .white{background-color:#E1DB9B}.color .olong{display:inline-flex;justify-content:center;align-items:center}.color .olong .big-O{display:inline-flex;justify-content:center;align-items:center}.color .olong .o{position:absolute;font-size:55%}.color .text{display:inline-block;font-family:'Prompt', serif;text-transform:uppercase;font-size:1.8rem}

.input--temperature{width:100%;height:75%;position:absolute}.temperature{display:grid;grid-template-areas:"a" "value" "label";grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr;position:relative}.temperature .value{grid-area:value;display:flex;justify-content:center;align-items:center;line-height:1.1}.temperature .value .amount{font-size:4.5rem}.temperature .label{grid-area:label;display:flex;justify-content:center;align-items:center}.temperature .label .amount-label:first-child{width:50%;margin-right:.25rem}.temperature .label .amount-label:last-child{width:50%;margin-left:0.25rem}.circle{grid-area:value;display:flex;justify-content:center;align-items:flex-end;min-width:8rem;min-height:5.5rem;border:2px solid #A0A0A0;border-top:none;border-radius:100%;margin-bottom:0.5rem}

.timer{width:150px;margin:0 auto}.input--timer{width:100%;height:100%;position:absolute}.capsule{display:flex;justify-content:space-between;align-items:center;margin:0 auto;width:9rem;padding:0;padding-left:.5rem;padding-right:1rem;border:2px solid #A0A0A0;border-radius:500px;position:relative}.capsule .label{font-size:1.1rem}.values{display:inline-flex;justify-content:space-between;align-items:center;width:4.8rem}.values .value{line-height:1.05}.value{display:inline-block;min-width:1ch;height:4.5rem;font-size:4.5rem;text-align:center;line-height:1}.sec{position:relative;top:-2px}

.name .name__name{border-bottom:1px solid #5F5E5E}

.weight{display:flex;justify-content:center;align-items:center;position:relative}.input--weight{width:100%;height:75%;margin-top:-15%;position:absolute}.diamond{grid-area:diamond;display:flex;justify-content:center;align-items:center;width:6rem;height:6rem;border:2px solid #A0A0A0;transform-origin:center;transform:rotate(45deg);position:absolute;pointer-events:none}.diamond .value{font-size:4.5rem;transform:rotate(-45deg)}.diamond .tsp,.diamond .g{position:absolute;pointer-events:all}.diamond .tsp{transform:rotate(-45deg);top:6.6rem}.diamond .g{transform:translate(4.2rem, .4rem) rotate(-45deg)}

.input--water{width:100%;height:75%;position:absolute}.water{display:grid;grid-template-areas:"a" "value" "label";grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr;position:relative}.water .value{grid-area:value;display:flex;justify-content:center;align-items:center}.water .value .amount{font-size:4.5rem}.water .label{grid-area:label;display:flex;justify-content:center;align-items:center}.water .label .amount-label:first-child{width:50%;margin-right:.25rem}.water .label .amount-label:last-child{width:50%;margin-left:0.25rem}.glass{grid-area:value;display:flex;justify-content:center;align-items:flex-end;min-width:7.5rem;min-height:5.2rem;border:2px solid #A0A0A0;border-top:none;margin-bottom:0.3rem}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

html,body{width:100vw;height:100vh;font-family:'Oranienbaum', serif;font-size:16px;color:#5F5E5E}@media screen and (min-width: 425px){html,body{background-color:#fcfffd}}main{width:100vw;height:100vh;display:grid;grid-template-areas:'controls controls' 'name name' 'color weight' 'gap1 gap1' 'temp water' 'gap2 gap2' 'timer brew';grid-template-columns:1fr 1fr;grid-template-rows:4% 18% 20% 3% 20% 3% 20%;overflow:hidden;background-color:white;position:relative}@media screen and (min-width: 425px){main{width:425px;margin:0 auto;border:1px solid #f7f7f7}}input,button{-webkit-appearance:none;-moz-appearance:none;appearance:none;border:none;background:none;font-family:inherit;color:inherit}input:focus,button:focus{outline:none}input{opacity:0;position:absolute}@keyframes done{0%{opacity:0;background-color:rgba(255,119,37,0.75)}9%{opacity:1}35%{opacity:1;background-color:rgba(255,255,255,0)}100%{opacity:1}}@keyframes out{0%{opacity:1}100%{opacity:0}}.overlay{width:calc(100% - 22px);height:calc(100% - 22px);position:absolute;border:solid 11px rgba(255,119,37,0.52);opacity:0;z-index:20;pointer-events:none}.overlay.done{animation:done 8.0s forwards;pointer-events:all}.name{grid-area:name}.name .name__name{font-size:2.5rem}.controls{grid-area:controls}.name{grid-area:name}.temperature{grid-area:temp}.color{grid-area:color}.weight,.water,.timer,.temperature,.color,.brew{transform-origin:center;transform:scale(0.85);min-width:100%;min-height:100%}.weight{grid-area:weight}.water{grid-area:water}.timer{grid-area:timer}.brew{grid-area:brew}.f-cc{display:flex;justify-content:center;align-items:center}.amount-label{font-size:1.8rem;color:#c7c4c4}.amount-label.active{color:#5F5E5E}

